select{
    padding: 14px 24px;
    width: 300px;
    height: 48px;
    background: #F7F7F7;
    border-radius: 2px;
    border: 1px solid transparent;
    font-size: 14px;
    font-family: 'Inter', sans-serif;
    cursor: pointer;
}
option{
    background: #F7F7F7;
    box-shadow: 0px 0px 8px rgba(161, 157, 157, 0.15);
}
#year-drop{
    position: absolute;
    left: 0;
    transition: all ease-in;
}
#sub-drop{
    position: absolute;
    left: 56%;
    top: 49%;
}
#year-drop:hover{
    border-style: solid;
    border: 1px solid #007BAB;
}
#batch-drop{
    position: absolute;
    left: 28%;
